import { Component, HostBinding, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormBuilder } from '@angular/forms';

import { differenceInMinutes } from 'date-fns';
import { timer } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

import { RecordService } from '@attendance-system/data/services';
import { AsDialog, AsDialogRef } from '@attendance-system/shared/ui/dialog';

const CLOCK_DEBOUNCE_MINUTES = 9.5 * 60;

type ClockType = 'IN' | 'OUT';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  @ViewChild('editTimeTpl', { static: true }) editTimeTpl: TemplateRef<void>;
  @HostBinding('className') className = 'd-flex justify-content-center pt-2';

  currentTime$ = timer(0, 1000).pipe(
    map(() => new Date()),
    shareReplay(1)
  );

  form = this.fb.group({ remark: [null] });

  editTimeDialogRef: AsDialogRef<any, any>;
  editTimeForm = this.fb.group({ hour: [0], minute: [0] });

  startTime = null;
  endTime = null;

  readonly weekDay = {
    Monday: '星期一',
    Tuesday: '星期二',
    Wednesday: '星期三',
    Thursday: '星期四',
    Friday: '星期五',
    Saturday: '星期六',
    Sunday: '星期日'
  };
  readonly hours = Array.from({ length: 24 }, (_, i) => i);
  readonly minutes = Array.from({ length: 60 }, (_, i) => i);

  constructor(private fb: FormBuilder, private recordService: RecordService, private dialog: AsDialog) {}

  ngOnInit(): void {
    this.reloadRangeRecord();
  }

  reloadRangeRecord(): void {
    this.recordService.getRange().subscribe(({ startTime, endTime, remark }) => {
      this.startTime = startTime;
      this.endTime = endTime;
      this.form.reset({ remark });
      alert(`今日的上班打卡時間為 ${startTime}，下班打卡時間為 ${endTime}，備註為 ${remark}`);
    });
  }

  onSubmit(clockType: ClockType): void {
    if (clockType === 'OUT' && this.startTime && !this.endTime) {
      if (differenceInMinutes(new Date(this.startTime), new Date()) > CLOCK_DEBOUNCE_MINUTES) {
        alert('下班打卡如果超過 9.5 小時，則擋著不給打下班卡！');
        return;
      }
    }

    if ((clockType === 'IN' && this.startTime) || (clockType === 'OUT' && this.endTime)) {
      this.openEditTimeTpl(clockType);
      return;
    }

    const remark = this.form.get('remark').value;
    this.recordService.create(remark).subscribe(() => {
      this.reloadRangeRecord();
      alert(`今日的${clockType === 'IN' ? '上班' : '下班'}打卡時間已新增!!`);
    });
  }

  openEditTimeTpl(clockType: ClockType): void {
    const defaultTime = new Date(clockType === 'IN' ? this.startTime : this.endTime);

    this.editTimeForm.reset({
      hour: defaultTime.getHours(),
      minute: defaultTime.getMinutes()
    });

    this.editTimeDialogRef = this.dialog.open(this.editTimeTpl, {
      clockType
    });

    this.editTimeDialogRef.afterClosed$.subscribe(() => {
      this.editTimeDialogRef = null;
    });
  }

  onEditTimeSubmit(clockType: ClockType): void {
    const { hour, minute } = this.editTimeForm.value;

    let dateLeft: Date;
    let dateRight: Date;

    if (clockType === 'IN') {
      const endTime = new Date(this.endTime);
      dateRight = new Date(endTime.getFullYear(), endTime.getMonth(), endTime.getDate(), hour, minute);
      dateLeft = new Date(
        endTime.getFullYear(),
        endTime.getMonth(),
        endTime.getDate(),
        endTime.getHours(),
        endTime.getMinutes()
      );

      if (differenceInMinutes(dateLeft, dateRight) < 0) {
        alert('上班打卡時間不能大於下班打卡時間！');
        return;
      }
    } else {
      const startTime = new Date(this.startTime);
      dateLeft = new Date(startTime.getFullYear(), startTime.getMonth(), startTime.getDate(), hour, minute);
      dateRight = new Date(
        startTime.getFullYear(),
        startTime.getMonth(),
        startTime.getDate(),
        startTime.getHours(),
        startTime.getMinutes()
      );

      if (differenceInMinutes(dateLeft, dateRight) < 0) {
        alert('下班打卡時間不能小於上班打卡時間！');
        return;
      }
    }

    if (differenceInMinutes(dateLeft, dateRight) > CLOCK_DEBOUNCE_MINUTES) {
      alert('打卡時間超過 9.5 小時，請重新輸入！');
      return;
    }

    this.editTimeDialogRef.close();

    // TODO: 更新打卡時間

    alert(`今日的${clockType === 'IN' ? '上班' : '下班'}打卡時間已更新!!`);
  }
}
